:root {
  --bg-primary: #04060b;
  --bg-card: rgba(13, 17, 32, 0.7);
  --border-light: rgba(99, 102, 241, 0.12);
  --border-heavy: rgba(99, 102, 241, 0.25);
  --accent: #6366F1;
  --accent-glow: rgba(99, 102, 241, 0.4);
}

/* Living Background - Subtle Noise + Animated Gradient */
.living-bg {
  position: fixed;
  inset: 0;
  z-index: -1;
  background-color: var(--bg-primary);
  background-image:
    radial-gradient(at 0% 0%, rgba(99, 102, 241, 0.1) 0px, transparent 50%),
    radial-gradient(at 100% 100%, rgba(139, 92, 246, 0.1) 0px, transparent 50%);
}

.living-bg::after {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  opacity: 0.03;
  pointer-events: none;
}

/* Glassmorphism 2.0 - Double Borders & Heavy Blur */
.glass-card {
  background: var(--bg-card);
  backdrop-filter: blur(24px) saturate(180%);
  -webkit-backdrop-filter: blur(24px) saturate(180%);
  border: 1px solid var(--border-light);
  box-shadow:
    0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06),
    inset 0 1px 1px 0 rgba(255, 255, 255, 0.05);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.glass-card:hover {
  border-color: var(--border-heavy);
  box-shadow:
    0 20px 25px -5px rgba(0, 0, 0, 0.2),
    0 10px 10px -5px rgba(0, 0, 0, 0.1),
    0 0 20px 0 rgba(99, 102, 241, 0.1);
}

/* Neon Glow Utilities */
.glow-primary {
  filter: drop-shadow(0 0 8px var(--accent-glow));
}

.glow-text {
  text-shadow: 0 0 12px var(--accent-glow);
}

/* Gradient Text */
.gradient-text {
  background: linear-gradient(135deg, #818cf8 0%, #c084fc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #04060b;
}

::-webkit-scrollbar-thumb {
  background: #1e293b;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #334155;
}

/* Input styles */
.fin-input {
  background: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  color: #f1f5f9;
  transition: all 0.2s;
  width: 100%;
  padding: 0.875rem 1.25rem;
  font-size: 0.95rem;
  outline: none;
}

.fin-input:focus {
  border-color: var(--accent);
  background: rgba(99, 102, 241, 0.05);
  box-shadow: 0 0 0 4px rgba(99, 102, 241, 0.1);
}

/* Button styles */
.btn-primary {
  background: linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%);
  color: white;
  padding: 0.875rem 2rem;
  border-radius: 12px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 0 0 0 rgba(99, 102, 241, 0);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 0 15px rgba(99, 102, 241, 0.4);
}

.btn-primary:active {
  transform: translateY(0);
}

.btn-ghost {
  background: rgba(255, 255, 255, 0.03);
  color: #94a3b8;
  padding: 0.875rem 2rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  cursor: pointer;
  transition: all 0.2s;
}

.btn-ghost:hover {
  background: rgba(255, 255, 255, 0.06);
  border-color: rgba(255, 255, 255, 0.1);
  color: white;
}

/* Step indicator */
.step-active {
  background: linear-gradient(135deg, #6366F1, #8B5CF6);
  box-shadow: 0 0 15px rgba(99, 102, 241, 0.3);
}

.step-done {
  background: #10B981;
}

.step-inactive {
  background: rgba(255, 255, 255, 0.05);
  color: #64748B;
}

